* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen-Sans, Ubuntu, Cantarell,
    'Helvetica Neue', sans-serif;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

img {
  display: block;
  max-width: 100%;
}

a {
  text-decoration: none;
  outline: none;
}
input {
  font: inherit;
}
.main-html {
  list-style-type: none;
  margin: 10px;
  padding: 50px;
}

.body-switcher {
  background-color: aqua;
}

.timer_header {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  margin-bottom: 20px;
}

.timer_body {
  font-family: 'Helvetica Neue', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', sans-serif;
  line-height: 1.5;
  color: #2a2a2a;
  background-color: rgb(202, 198, 198);
}
.timer {
  font-family: sans-serif;
  color: #fff;

  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  margin: -3px;

  font-weight: 100;
  text-align: center;
  font-size: 30px;
}
.field {
  padding: 10px;
  border-radius: 3px;
  background: #111011;

  width: 150px;
  margin: 3px;
}

.value {
  padding: 15px;
  border-radius: 3px;
  background: #474746;
  display: inline-block;
  width: 100px;
}

.label {
  display: block;
  padding-top: 5px;
  font-size: 16px;
}

.text {
  color: black;
  text-align: center;
  font-family: 'Jura', sans-serif;
}
